
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.controls {
	display: grid;
	grid-template-columns: auto auto auto;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	grid-row-gap: 16px;
}
html {
	height: 100%;
    background: #333;
}
* {
	text-align: center;
}
.heading {
	font-family: Arial;
	margin-top:40vh;
	color: #e5e5e5;
}
.URL-input, .convert-button {
	font-size:1.3em;
	padding:5px 10px;
}
.URL-input {
	border-radius:4px 0px 0px 4px;
	width:30em;
	text-align: left;
	border:2px solid #5a5959;
	outline:none;
	background: #4c4c4c;
}
.URL-input:focus {
	border:2px solid #0485ff;
}
.convert-button {
	border:2px solid #0485ff;
	background: #0485ff;
	color:white;
	-webkit-transition: 0.15s;
	transition: 0.15s;
}
.convert-button:hover {
	background: #016acc;
	border-color: #016acc;
}
#mp3 {
	border-radius:0px 4px 4px 0px;
}
